'use client';
import { useAuth } from "@/context/auth-context";
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Download, Upload, FileText, CheckCircle, Clock } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { subjects } from "@/lib/mock-data";

const assignments = [
    { id: 'assign1', title: 'Algorithm Analysis Homework', subjectId: 'subj1', dueDate: '2024-10-26', status: 'Submitted' },
    { id: 'assign2', title: 'ML Project Proposal', subjectId: 'subj2', dueDate: '2024-10-28', status: 'Pending' },
    { id: 'assign3', title: 'SQL Query Challenge', subjectId: 'subj3', dueDate: '2024-11-02', status: 'Pending' },
    { id: 'assign4', title: 'OS Kernel Module Report', subjectId: 'subj4', dueDate: '2024-11-05', status: 'Graded' },
];

export default function AssignmentsPage() {
    const { role } = useAuth();
    
    if (!role) return null;

    if (role === 'faculty') {
        return (
            <div className="flex flex-col gap-6">
                <div>
                    <h1 className="text-3xl font-bold">Manage Assignments</h1>
                    <p className="text-muted-foreground">Create, view, and grade assignments.</p>
                </div>
                <Button className="self-start">
                    <Upload className="mr-2" />
                    Create New Assignment
                </Button>
                {/* Faculty view to be built out further */}
            </div>
        )
    }

    return (
        <div className="flex flex-col gap-6">
            <div>
                <h1 className="text-3xl font-bold">Your Assignments</h1>
                <p className="text-muted-foreground">Download materials and submit your work.</p>
            </div>

            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
                {assignments.map(assignment => {
                    const subject = subjects.find(s => s.id === assignment.subjectId);
                    return (
                        <Card key={assignment.id}>
                            <CardHeader>
                                <CardTitle className="flex items-start gap-2">
                                    <FileText className="h-6 w-6" style={{color: subject?.color}}/>
                                    <span>{assignment.title}</span>
                                </CardTitle>
                                <CardDescription style={{color: subject?.color}}>{subject?.name}</CardDescription>
                            </CardHeader>
                            <CardContent className="flex flex-col gap-2">
                                <p className="text-sm text-muted-foreground">Due Date: {assignment.dueDate}</p>
                                <div>
                                {assignment.status === 'Submitted' && <Badge variant="secondary"><CheckCircle className="mr-1 h-3 w-3 text-green-500" />Submitted</Badge>}
                                {assignment.status === 'Pending' && <Badge variant="outline"><Clock className="mr-1 h-3 w-3" />Pending</Badge>}
                                {assignment.status === 'Graded' && <Badge>Graded</Badge>}
                                </div>
                            </CardContent>
                            <CardFooter className="flex justify-between">
                                <Button variant="outline"><Download className="mr-2"/> Download</Button>
                                <Button disabled={assignment.status !== 'Pending'}><Upload className="mr-2"/> Submit</Button>
                            </CardFooter>
                        </Card>
                    );
                })}
            </div>
        </div>
    );
}
